import React, { Fragment } from 'react'
import {HashRouter, Route, Switch, Redirect} from 'react-router-dom'
import Home from './pages/home'
import './popup.styl'

function Popup() {
    return (
        <Fragment>
            <HashRouter>
                <Switch>
                    <Route path="/home" component={Home} />
                    <Redirect to={'/home'} />
                </Switch>
            </HashRouter>
        </Fragment>
    )
}

export default Popup