/* eslint-disable no-undef */
import { message, Spin } from "antd";
import React, { useState, useEffect } from "react";
import "./index.styl";



function Home() {
  const [downloadLinks, setDownloadLinks] = useState([]);
  const [videoData, setVideoData] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    chrome.runtime.onMessage.addListener(function (request, sender, sendResponse) {
      console.log('收到来自content-script的消息：');
      sendResponse('我是后台，我已收到你的消息：' + JSON.stringify(request));
      if (request.type === 'sendVideoDataFromContent') {
        setLoading(false);
        console.log('信息已获取', request)
        const { result, videoData: vData } = request || {};
        const { durl } = result.data || {};
        if (durl && durl.length) {
          message.success('获取到以下链接')
          const links = durl.map(d => d.url || d.backup_url);
          setVideoData(vData)
          setDownloadLinks(links)
        } else {
          message.error('未获取到链接，请刷新重试')
        }
      }
    });
    
    chrome.tabs.query({active: true, currentWindow: true},function(tabs) {
      chrome.tabs.sendMessage(tabs[0].id, { type: 'getVideoData' });
    });
    
  }, []);
  return (
    <Spin spinning={loading} size="large">
      <div className="container">
        <div className='bg bg-blur' style={{ backgroundImage: 'url("./bg.jpg")' }}></div>
        <div className="content">
          <div className="title">{videoData?.title || ''}</div>
          <ul className="search-result">
            {(downloadLinks || []).map((link, key) => {
              return (
                <li className="download-link" key={key} style={{ margin: '12px 0' }}>
                  <a href={link} download={`video-${key + 1}`}>{`链接${key + 1} 点我下载`}</a>
                </li>
              )
            })}
          </ul>
        </div>
        <div className="rewardcode">
          <div className="rewardtext">如果本插件对你有所帮助的话，那就请作者喝杯咖啡☕️吧~！（づ￣3￣）づ╭❤～</div>
          <img src="./rewardcode.jpg" />
        </div>
      </div>
    </Spin>
  );
}
export default Home;
